import { createSlice } from '@reduxjs/toolkit'
import { doGet } from '../../services/RemoteCalls';

let store = null;
const injectStore = (injectedStore) => { store = injectedStore }

const portfolioSlice = createSlice({
    name: "portfolio",
    initialState: {
        condors: []
    },
    reducers: {
        onCondorPortfolioReceived: (state, action) => {
            state.condors = action.payload;
            state.condors.forEach( (condor) => condor.lastTickTime = Date.now() );
        },
        onCondorPriceReceived: (state, action) => {
            let description = action.payload.description;
            let condor = state.condors.find( (condor) => {
                return condor.description === description;
            } );
            condor.price = action.payload.price;
            condor.lastTickTime = Date.now();
        },
        onCondorLegPriceReceived: (state, action) => {
            let description = action.payload.description;
            let condor = state.condors.find( (condor) => {
                return condor.description === description;
            } );
            let optionDescription = action.payload.optionDescription;
            let leg = condor.legs.find( (leg) => leg.description === optionDescription );
            leg.price = action.payload.price;
        }
    }
});

export const fetchPortfolio = () => {
    const baseUrl = store.baseUrl;
    console.log(`Got base URL of ${baseUrl} from store in portfolio slice.`)
    return async (dispatch) => {
        await doGet(baseUrl + "/positions").then( (positions) => {
            dispatch(onCondorPortfolioReceived(positions));
        }).catch( () => {
            console.error( "Error fetching initial positions" );
        });
    }
}

export const {onCondorPortfolioReceived, onCondorPriceReceived, onCondorLegPriceReceived} = portfolioSlice.actions;
export {injectStore as injectPortfolioStore};
export default portfolioSlice.reducer;