import { createSlice } from '@reduxjs/toolkit'
import { subscribeTopic } from '../../services/StompClient';
import _ from 'lodash';

const pricesSlice = createSlice({
    name: "tradingStatus",
    initialState: {
        spxPrice: {
            last: 0.0,
            change: 0.0,
            changePct: 0.0
        },
        condorPrice: null,
        condor: null,
        impVol: 0.0,
        lastCondorReceivedTime: 0
    },
    reducers: {
        onSPXPriceReceived: (state, action) => {
            state.spxPrice = action.payload;
        },
        onCondorTickReceived: (state, action) => {
            state.condorPrice = action.payload;
        },
        onNewCondorReceived: (state, action) => {
            //Are we just getting the same condor again? Skip it.
            const newCondor = action.payload;
            const oldCondor = state.condor;
            if( _.isEqual(newCondor, oldCondor)) {
                return;
            }
            state.lastCondorReceivedTime = Date.now();
            state.condor = action.payload;
            if (!state.condorPrice) {
                state.condorPrice = {};
            }
            state.condorPrice.bid = state.condorPrice.ask = state.condorPrice.midpoint = null;
        },
        onImpVolReceived: (state, action) => {
            state.impVol = action.payload;
        }
    }
});

export const subscribeSPXTicks = () => {
    return async (dispatch) => {
        subscribeTopic('/topic/prices.spx', (spxPrice) => {
            dispatch(onSPXPriceReceived(spxPrice));
        });
    }
}

export const subscribeCondorPrice = () => {
    return async (dispatch) => {
        subscribeTopic('/topic/prices.condor', (condorTick) => {
            dispatch(onCondorTickReceived(condorTick));
        });
    }
}

export const subscribeCondor = () => {
    return async (dispatch) => {
        subscribeTopic("/topic/prices.current.condor", (condor) => {
            dispatch(onNewCondorReceived(condor));
        });
    }
}

export const subscribeImpVol = () => {
    return async (dispatch) => {
        subscribeTopic("/topic/prices.impvol", (impVol) => {
            dispatch(onImpVolReceived(impVol));
        });
    }
}

export const { onSPXPriceReceived, onCondorTickReceived, onNewCondorReceived, onImpVolReceived, setNewCondor } = pricesSlice.actions

export default pricesSlice.reducer
