import React, { useEffect, useState, useRef } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { doPost } from '../../services/RemoteCalls';
import '../../App.css';
import { FormControlLabel } from '@mui/material';
import IconSwitch from '../../components/IconSwitch';
import { switchStyle } from '../../components/switchStyle';
import { isNumber } from '../../app/utils';
import { onConnectedChanged } from './tradingSlice';

function TradingStatusPage() {
  const connected = useSelector( state => state.connection.connected );

  const spxPrice = useSelector(state => state.prices.spxPrice.last);
  const spxChange = useSelector(state => state.prices.spxPrice.change);
  const spxChangePct = useSelector(state => state.prices.spxPrice.changePct);
  const condorPrice = useSelector(state => state.prices.condorPrice);
  const alreadyTraded = useSelector(state => state.trading.alreadyTraded);
  const unableToTrade = useSelector(state => state.trading.unableToTrade);
  const unableToTradeReason = useSelector(state => state.trading.unableToTradeReason);
  const tradingHalt = useSelector(state => state.trading.tradingHalt);
  const tradingHaltOverridden = useSelector(state => state.trading.tradingHaltOverridden);
  const tradingEnabled = useSelector(state => state.trading.tradingEnabled);
  const invalidCondor = useSelector(state => state.trading.invalidCondor);
  const timeUntilTrading = useSelector(state => state.trading.timeUntilTrading);
  const bought = useSelector(state => state.trading.bought);
  const fillPrice = useSelector(state => state.trading.fillPrice);
  const tradingDay = useSelector(state => state.trading.tradingDay);
  const expirationIsNotTradingDay = useSelector(state => state.trading.expirationNotTradingDay);
  const impVol = useSelector(state => state.prices.impVol);
  const condorBid = useSelector( state => state.trading.condorBid);
  const condor = useSelector(state => state.prices.condor);
  const lastCondorReceivedTime = useSelector(state => state.prices.lastCondorReceivedTime);

  const [newCondor, setNewCondor] = useState(false);
  const [oldLastCondorReceivedTime, setOldLastCondorReceivedTime] = useState(lastCondorReceivedTime);

  const dispatch = useDispatch();

  useEffect(() => {
      dispatch(onConnectedChanged(connected));
      // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [connected]);

  const waitHandle = useRef(null);
  useEffect(() => {
    if( oldLastCondorReceivedTime === 0 || lastCondorReceivedTime === oldLastCondorReceivedTime ) {
      setOldLastCondorReceivedTime( lastCondorReceivedTime );
      return;
    }
    setNewCondor(true);
    if( waitHandle.current ) {
      clearTimeout(waitHandle.current);
    }
    waitHandle.current = setTimeout(() => {
      setNewCondor(false);
    }, 3000);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [lastCondorReceivedTime]);

  function willTradeToday() {
    return !invalidCondor && !alreadyTraded && ( !tradingHalt || tradingHaltOverridden ) && tradingEnabled;
  }

  async function setTradingEnabledOnBackend(tradingEnabledValue) {
    const baseUrl = process.env.REACT_APP_API_URL;
    const url = baseUrl + "/trading-enabled";
    await doPost(url, tradingEnabledValue).then( () => {
      console.log("Set trading " + (tradingEnabledValue?"enabled":"disabled"));
    }).catch( (err) => {
      console.warn( "Failed setting trading enabled: " + err );
    });; 
  }

  function toggleTradingEnabled() {
    const newEnabled = !tradingEnabled;
    setTradingEnabledOnBackend(newEnabled);
  }

  function tradingStateString() {
    if( !tradingDay ) {
      return !connected ? "Waiting for connection..." : "Market closed today."
    } else if( expirationIsNotTradingDay ) {
      return "Market closed on expiration.";
    } else if ( tradingHalt && !tradingHaltOverridden ) {
      return "Trading halted today.";
    } else if( bought ) {
      return `Bought condor at ${fillPrice}`;
    } else if( unableToTrade ) {
      return `Unable to trade today. (Reason: ${unableToTradeReason})`;
    } else if( alreadyTraded || (timeUntilTrading && timeUntilTrading.tradingEnded) ) {
      return "Trading ended for today.";
    } else if( timeUntilTrading && timeUntilTrading.tradingStarted ) {
      return `Trading under way (Bid: ${condorBid}).`;
    } else if( invalidCondor ) {
      return "One or more condor options not trading.";
    } else if( !willTradeToday() ) {
      return "Not trading today.";
    } else if( timeUntilTrading && timeUntilTrading.hours >= 0 && timeUntilTrading.minutes >= 0 ) {
      return `Time until trading starts: ${timeUntilTrading.hours}:${timeUntilTrading.minutes.toString().padStart(2,'0')}:${timeUntilTrading.seconds.toString().padStart(2,'0')}`;
    } else {
      return "Loading...";
    }
  }

  var canEnableTrading = !unableToTrade && !invalidCondor && tradingDay && !expirationIsNotTradingDay && ( ( !tradingHalt || tradingHaltOverridden ) && (timeUntilTrading && !timeUntilTrading.tradingEnded) );

  return (
    <div className="App">
      <div className="App-header">
        <div style={{display: "flex", flexDirection: "column", color: (willTradeToday()?"green":"red")}}>
          <FormControlLabel disabled={!canEnableTrading} sx={{color: tradingEnabled?"green":"red", justifyContent:"center"}} control={
            <IconSwitch checked={tradingEnabled && !invalidCondor} disabled={!canEnableTrading} onChange={toggleTradingEnabled}  sx={switchStyle} />} 
            label={<div style={{fontSize:"20pt"}}>{tradingEnabled?"Trading Enabled":"Trading Disabled"}</div>} />
          <div style={{color:bought?"blue":"inherit"}}>{tradingStateString()}</div>
          <div>Expiration</div>
          <div style={{marginBottom: "2vh"}}>{condor ? condor.expiration : "?"}{alreadyTraded ? (<div>Trading complete for today.</div>) : "" }</div>
        </div>
        <div>
          <div>Current SPX Price</div>
          <div style={{display: "flex", justifyContent:"space-between"}}><div>{isNumber(spxPrice) ? spxPrice.toFixed(2) : spxPrice}</div>&nbsp;&nbsp;{isFinite(spxChangePct)?<div style={{color:(spxChange<0.0?"red":"green")}}>{" (" +(spxChange<0.0?"":"+") + (Math.round(spxChange*100.0) / 100.0).toFixed(2) + " / " + (spxChangePct<0.0?"":"+") + (Math.round(spxChangePct*100.0) / 100.0).toFixed(2) + "%)"}</div>:""}</div>
        </div>
        <div style={{height: "10px"}}></div>
        <div style={{display: "flex", flexDirection: "column"}}>
          <div>Current 7-day Implied Vol</div>
          <div>{Math.round((impVol*100.0)*1000.0)/1000.0}%</div>
        </div>
        <div style={{height: "10px"}}></div>
        <div style={{display: "flex", flexDirection: "column", width: "100%", alignItems: "center"}}>
          <div>Condor</div>
          <div>{condor ? condor.expiration : "?"}</div>
          <div style={{display: "flex", flexDirection: "row", width: "50%", justifyContent: "space-between", alignItems: "center", color:(newCondor?"violet":"skyblue")}}>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Long put</label>
              <div>{condor && condor.strikes.length > 0 ? condor.strikes[3] : "?"}</div>
            </div>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Short put</label>
              <div>{condor && condor.strikes.length > 0 ? condor.strikes[2] : "?"}</div>
            </div>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Short call</label>
              <div>{condor && condor.strikes.length > 0 ? condor.strikes[1] : "?"}</div>
            </div>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Long call</label>
              <div>{condor && condor.strikes.length > 0 ? condor.strikes[0] : "?"}</div>
            </div>
          </div>
        </div>
        <div style={{height: "10px"}}></div>
        <div style={{display: "flex", flexDirection: "column", alignItems: "center", width: "100%"}}>
          <div>Condor Price</div>
          <div style={{display: "flex", flexDirection: "row", width: "50%", justifyContent: "space-between", alignItems: "center"}}>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Bid</label>
              <div>{condorPrice && condorPrice.bid ? condorPrice.bid : "?"}</div>
            </div>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Mid</label>
              <div>{condorPrice && condorPrice.midpoint ? condorPrice.midpoint : "?"}</div>
            </div>
            <div style={{display: "flex", flexDirection: "column"}}>
              <label>Ask</label>
              <div>{condorPrice && condorPrice.ask ? condorPrice.ask : "?"}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TradingStatusPage;
