import { Fragment, useState } from "react";
import { TableRow, TableCell, Collapse, Typography, Table, TableHead, TableBody, Box, IconButton } from "@mui/material"
import { formatDollarAmount, formatOptionPrice } from "../../app/utils"
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import { condorProfitLoss } from "../../app/utils";

export default function PortfolioTableRow(props) {
    const [open, setOpen] = useState(false);
    
    const isCondorPriceFresh = props.condor.lastTickTime && (Date.now() - props.condor.lastTickTime < 60000);
    const legsTotalPL = props.condor.legs.reduce( (sum, leg) => sum += (leg.price - leg.cost)*leg.quantity, 0 );
    const legsPrice = props.condor.legs.reduce( (sum, leg) => sum += leg.price * leg.quantity/Math.abs(leg.quantity), 0 );
    const profitLoss = isCondorPriceFresh && props.condor.price !== 0 ? props.condor.price - props.condor.cost : legsTotalPL;

    const condorPrice = () => {
        return isCondorPriceFresh && props.condor.price !== 0 ? props.condor.price : legsPrice;
    }

    return (
        <Fragment>
            <TableRow key={props.condor.description}>
                <TableCell style={{ width: "10px", minWidth: "10px", padding: "3px" }}>
                    <IconButton
                        aria-label="expand row"
                        size="small"
                        onClick={() => setOpen(!open)}
                    >
                        {open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
                    </IconButton>
                </TableCell>
                <TableCell>{props.condor.description + (props.condor.paperTrade ? " (Paper)" : "")}</TableCell>
                <TableCell>{props.condor.quantity}</TableCell>
                <TableCell style={{ textAlign: "right" }}>{formatOptionPrice(props.condor.cost)}</TableCell>
                <TableCell style={{ textAlign: "right" }}>{formatOptionPrice(condorPrice())}</TableCell>
                <TableCell style={{ textAlign: "right", paddingRight: "5em", color: profitLoss >= 0 ? "black" : "red" }}>{formatDollarAmount(condorProfitLoss(props.condor))}</TableCell>
            </TableRow>
            <TableRow>
                <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={6}>
                    <Collapse in={open} timeout="auto" unmountOnExit>
                        <Box sx={{ margin: 1 }}>
                            <Typography variant="h6" gutterBottom component="div">
                                Legs
                            </Typography>
                            <Table size="small" aria-label="purchases">
                                <TableHead>
                                    <TableRow>
                                        <TableCell>Option</TableCell>
                                        <TableCell>Strike</TableCell>
                                        <TableCell align="right">Quantity</TableCell>
                                        <TableCell align="right">Cost/Option</TableCell>
                                        <TableCell align="right">Price</TableCell>
                                        <TableCell align="right">P/L</TableCell>
                                    </TableRow>
                                </TableHead>
                                <TableBody>
                                    {props.condor.legs.map((leg) => {
                                        const optionPL = (leg.price - leg.cost) * leg.quantity;
                                        return (
                                            <TableRow key={leg.description}>
                                                <TableCell component="th" scope="row">
                                                    {"(SPXW) " + leg.option.expirationDateString + " " + leg.option.strike + " " + (leg.option.call ? "CALL" : "PUT")}
                                                </TableCell>
                                                <TableCell>{leg.option.strike}</TableCell>
                                                <TableCell align="right">{leg.quantity}</TableCell>
                                                <TableCell align="right">{formatOptionPrice(leg.cost)}</TableCell>
                                                <TableCell align="right">{formatOptionPrice(leg.price)}</TableCell>
                                                <TableCell align="right" style={{ color: optionPL >= 0 ? "black" : "red" }}>{formatDollarAmount(optionPL)}</TableCell>
                                            </TableRow>
                                        )
                                    })}
                                    <TableRow style={{outline:"groove"}}>
                                        <TableCell>Condor P/L</TableCell>
                                        <TableCell></TableCell>
                                        <TableCell></TableCell>
                                        <TableCell></TableCell>
                                        <TableCell></TableCell>
                                        <TableCell style={{textAlign:"right", color:legsTotalPL>=0?"black":"red"}}>{formatDollarAmount(legsTotalPL)}</TableCell>
                                    </TableRow>                                    
                                </TableBody>
                            </Table>
                        </Box>
                    </Collapse>
                </TableCell>
            </TableRow>
        </Fragment>


    )
}