import { Client } from '@stomp/stompjs';

let stompClient = null;
let connected = false;
let store = null;

const injectStore = (injectedStore) => { store = injectedStore }

async function initializeWebSockets(connectedCallback, disconnectedCallback) {

    if(connected || stompClient !== null) {
        return;
    }
    //So we don't try to connect twice.
    connected = true;

    const url = store.baseUrl + '/condors';
    console.log("Creating stomp client on URL " + url);
    stompClient = createClient(url, connectedCallback, disconnectedCallback);
}

function createClient(endpoint, connectedCallback, disconnectedCallback) {
    const client = new Client({
        brokerURL: endpoint,
        onConnect: () => {
            console.log("Stomp client connected.");
            connected = true;
            if( connectedCallback ) {
                connectedCallback();
            }
            store.dispatch( {type: "stomp/connected", payload: {connected: true}} );
        },
        onDisconnect: () => {
            console.warn("WebSocket disconnected.");
            connected = false;
            if( disconnectedCallback ) {
                disconnectedCallback();
            }
            store.dispatch( {type: "stomp/connected", payload: {connected: false}} );
        },
        onStompError: frame => {
            console.error("Stomp Error: " + frame);
        },
        onWebSocketClose: frame => {
            console.warn("WebSocket connection closed.");
            connected = false;
            if( disconnectedCallback ) {
                disconnectedCallback();
            }
            store.dispatch( {type: "stomp/connected", payload: {connected: false}} );
        }
    });
    client.activate();
    return client;
}

function subscribeTopic(topic, handler) {
    stompClient.subscribe(topic, message => {
        const received = JSON.parse(message.body);
        handler(received);
    });
}

export {initializeWebSockets, subscribeTopic, injectStore};