import { useSelector } from "react-redux";

//Mini-component to make figuring out the footer a teensy bit more readable
export default function StatusFooter(props) {
    const connected = useSelector( state => state.connection.connected );
    const tradingHaltOverridden = useSelector(state => state.trading.tradingHaltOverridden);
    const tradingHalt = useSelector(state => state.trading.tradingHalt);
    const liveTrading = useSelector(state => state.trading.liveTrading);

    var footer;
    if (!connected) {
        footer = <footer style={{ color: tradingHaltOverridden ? "green" : "red", fontWeight: "bolder" }}><div style={{ marginLeft: "10px" }}>Not connected.</div></footer>;
    } else {
        footer =
            <footer style={{ color: tradingHaltOverridden ? "green" : "red", fontWeight: "bolder" }}>
                {tradingHalt ? (<div style={{ marginLeft: "10px" }}>Trading halted until {tradingHalt.end}, reason: {tradingHalt.reason}{tradingHaltOverridden && "--Overridden"}</div>) : <div></div>}
                <div style={{ marginRight: "10px", color: "green", fontWeight: "800" }}>Trading: {liveTrading ? "Live" : "Paper"}</div>
            </footer>
    }
    return footer;
}