import React from "react";
import { Switch } from '@mui/material';
import { Check, Close } from "@mui/icons-material";

function IconThumb(props) {
    return <div style={{height: "20px", width: "20px", background: "green", display: "flex", overflow: "hidden", borderRadius: "10px"}}>
        {props.checked ?
        <Check sx={{ background: "green", color: "white", width: "20px", height: "20px"}} /> :
        <Close sx={{ background: "red", color: "black", width: "20px", height: "20px"}} />}
    </div>
}

function IconSwitch(props) {
    return <Switch checked={props.checked} disabled={props.disabled} onChange={props.onChange} sx={props.sx} 
                checkedIcon={<IconThumb checked={true} />} icon={<IconThumb checked={false} />} />
}

export default IconSwitch;