function padNumber(num, numDecimals) {
    const numString = num.toString();
    const decimal = numString.indexOf(".");
    if( decimal !== -1 && numString.length - decimal > numDecimals ) {
        return numString.slice(0, decimal + numDecimals + 1);
    }
    return decimal === -1 ? numString + ".00" : numString.slice(0,decimal) + numString.slice(decimal).padEnd(numDecimals + 1,'0');
}
export function formatOptionPrice(price) {
    const priceValue = Math.round((price) * 100) / 100;
    return padNumber(priceValue, 2);
}
export function formatDollarAmount(amount) {
    //Each option is worth $100
    let amountValue = amount*100;
    //Then round the value to 2 decimal places.
    amountValue = Math.round((amountValue + Number.EPSILON ) * 100) / 100;
    let numString = padNumber(amountValue, 2);
    return numString.startsWith("-") ? "-$" + numString.slice(1) : "$" + numString;
}


export function isNumber(value) {
    return typeof value === 'number' && isFinite(value);
}

/**
 * Returns either the tick-based P&L for the given condor or the legs-based P&L for the given condor, depending on whether
 * or not we've received a tick
 * @param {*} condor the condor to get the P&L for.
 */
export function condorProfitLoss(condor) {
    const isCondorPriceFresh = condor.lastTickTime && (Date.now() - condor.lastTickTime < 60000);
    if( condor.description === 'SPX Jun-11-24 5195.0/5205.0 5375.0/5385.0') {
        console.log(`Condor price ${isCondorPriceFresh?"is":"is not"} fresh`)
    }
    const isCondorPriceNonzero = condor.price !== 0;
    const profitLoss = (isCondorPriceFresh && isCondorPriceNonzero) ? (condor.price - condor.cost) * condor.quantity : condor.legs.reduce( (sum, leg) => sum += (leg.price - leg.cost)*leg.quantity, 0 );
    return profitLoss;
}
