import { configureStore } from '@reduxjs/toolkit'
import tradingStatusSlice from '../features/trading/tradingSlice'
import portfolioSlice from '../features/portfolio/portfolioSlice'
import pricesSlice from '../features/prices/pricesSlice'

function connectedReducer( state = { connected: false }, action ) {
  if( action.type === "stomp/connected" ) {
    state = {
      ...state,
      connected: action.payload.connected
    }
  }
  return state;
}

const fullStore = configureStore({
  reducer: {
    connection: connectedReducer,
    trading: tradingStatusSlice,
    portfolio: portfolioSlice,
    prices: pricesSlice
  }
});

const store = fullStore;

export default store;

export { fullStore };