async function doGet(url) {
    const options = {
        headers: {
            "Accept": "*/*",
            "Accept-Encoding": "gzip, deflate, br",
        },
        method: 'GET',
        mode: "cors"
    }

    let response = await fetch(url, options).then(response => {
      if( !response.ok ) {
        throw new Error("GET failed with error status " + response.status);
      }
      return response;
    }).catch( response => {
      return response;  //Should be of type Error
    });

    if( response instanceof Error ) {
      throw new Error("Could not complete GET call");
    }

    let json = await response.json();
    return json;

}

async function doPost(url, payload) {
    const user = "felliot010560@yahoo.com";
    const password = "c639eea9-485e-4b3e-a69a-d87bb1a55130";
    const auth = user + ":" + password;
    // eslint-disable-next-line no-unused-vars
    const auth64 = window.btoa(auth);
    const options = {
      headers: {
        "Accept": "*/*",
        "Accept-Encoding": "gzip, deflate, br",
        "Content-Type": "application/json"
      },
      method: 'POST',
      body: payload
    }

    var contentLength;
    let response = await fetch(url, options).then(response => {
      if( !response.ok ) {
        throw new Error("POST failed with error status " + response.status);
      }
      contentLength = response.headers.get("Content-Length");
      if( contentLength != null ) {
        contentLength = Number(contentLength);
      }
      return response;
    }).catch( response => {
      return response;  //Should be of type Error
    });

    if( response instanceof Error ) {
      throw new Error("Could not complete POST call");
    }

    if(contentLength === 0) {
      return;
    }

    let json = await response.json().catch( (e) => {
      console.log("Error getting json", e);
      return null;
    });;
    return json;
  }
