import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { AppBar, Toolbar, Button } from '@mui/material';
import StatusFooter from './components/StatusFooter';
import './App.css';
import TradingStatusPage from './features/trading/TradingStatusPage';
import PortfolioPage from './features/portfolio/PortfolioPage';
import logo from './iron-condor.png';
import { subscribeTradingTime, subscribeTradingState, subscribeCondorBid } from './features/trading/tradingSlice';
import { subscribeSPXTicks, subscribeImpVol, subscribeCondor, subscribeCondorPrice } from './features/prices/pricesSlice';
import { injectStore, initializeWebSockets } from './services/StompClient';
import { injectPortfolioStore } from './features/portfolio/portfolioSlice';
import store from './app/store';

function App() {
  const [pageToShow, setPageToShow] = useState("trading");
  const dispatch = useDispatch();
  console.log(`Environment is ${process.env.NODE_ENV}, URL (from env) is ${process.env.REACT_APP_API_URL}, test thing is ${process.env.REACT_APP_TEST_THING}`)
  const baseUrl =  process.env.REACT_APP_API_URL || ( window.location.protocol + '//' + window.location.host );
  console.log(`Base URL is ${baseUrl}`)

  useEffect( () => {
    store.baseUrl = baseUrl;
    async function startWebSocketConnection() {
      injectStore(store);
      injectPortfolioStore(store);
      const onConnected = () => {
        dispatch(subscribeSPXTicks());
        dispatch(subscribeCondorPrice());
        dispatch(subscribeCondor());
        dispatch(subscribeTradingTime());
        dispatch(subscribeTradingState());
        dispatch(subscribeImpVol());
        dispatch(subscribeCondorBid());
      };
      const onDisconnect = () => {
      }
      initializeWebSockets(onConnected, onDisconnect);
    };
    startWebSocketConnection();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);


  let returnComponents = [];
  returnComponents.push(<AppBar key="appBar"><Toolbar><div className="App-logo-holder"><img src={logo} className="App-logo" alt="logo" /></div><Button variant="text" size="small" sx={{color: pageToShow==='trading'?"yellow":"white"}} onClick={() => setPageToShow("trading")}>Trading</Button><Button variant="text" size="small" sx={{color: pageToShow==='portfolio'?"yellow":"white"}} onClick={() => setPageToShow("portfolio")}>Portfolio</Button></Toolbar></AppBar>);
  returnComponents.push(pageToShow === "trading" ? <TradingStatusPage key="tradingStatusPage" /> : <PortfolioPage key="portfolioPage" />);
  returnComponents.push(<StatusFooter key="statusFooter" />);
  return returnComponents;
}

export default App;
