import { useSelector, useDispatch } from "react-redux";
import { useEffect } from "react";
import PortfolioTableRow from "./PortfolioTableRow";
import { TableContainer, Table, TableBody, Paper, TableHead, TableCell, TableRow } from "@mui/material";
import { fetchPortfolio } from "./portfolioSlice";
import { formatDollarAmount } from "../../app/utils";
import { subscribeTopic } from "../../services/StompClient";
import { onCondorPortfolioReceived, onCondorPriceReceived, onCondorLegPriceReceived } from "./portfolioSlice";
import { isNumber, condorProfitLoss } from "../../app/utils";


export default function PortfolioPage(props) {
    const connected = useSelector(state => state.connection.connected);
    const condors = useSelector(state => state.portfolio.condors);
    const spxPrice = useSelector(state => state.prices.spxPrice.last);
    const spxChange = useSelector(state => state.prices.spxPrice.change);
    const spxChangePct = useSelector(state => state.prices.spxPrice.changePct);
    const dispatch = useDispatch();
    useEffect(() => {
        if (connected) {
            dispatch(fetchPortfolio());
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [connected]);

    useEffect(() => {
        if (!connected) {
            return;
        }
        const refreshTopic = "/topic/portfolio/refresh";
        subscribeTopic(refreshTopic, (portfolio) => {
            dispatch(onCondorPortfolioReceived(portfolio));
        });
        for (const condor of condors) {
            const topic = "/topic/portfolio/prices.condor." + condor.description;
            subscribeTopic(topic, (condorPrice) => {
                dispatch(onCondorPriceReceived(condorPrice));
            });
            const legTopic = "/topic/portfolio/prices.condor.leg." + condor.description;
            subscribeTopic(legTopic, (condorLegPrice) => {
                dispatch(onCondorLegPriceReceived(condorLegPrice));
            } );
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [connected, condors.length]);

    let total = condors.reduce((sum, condor) => sum += (condorProfitLoss(condor)), 0);

    return (
        <div className="App">
            <div className="App-header portfolio-page">
                <div>
                    <div>Current SPX Price</div>
                    <div style={{display: "flex", justifyContent:"space-between"}}><div>{isNumber(spxPrice) ? spxPrice.toFixed(2) : spxPrice}</div>&nbsp;&nbsp;<div style={{color:(spxChange<0.0?"red":"green")}}>{" (" +(spxChange<0.0?"":"+") + (Math.round(spxChange*100.0) / 100.0).toFixed(2) + " / " + (spxChangePct<0.0?"":"+") + (Math.round(spxChangePct*100.0) / 100.0).toFixed(2) + "%)"}</div></div>
                </div>
                {condors.length > 0 ?
                <TableContainer component={Paper} sx={{ marginTop: "1vh" }}>
                    <Table stickyHeader>
                        <TableHead>
                            <TableRow>
                                <TableCell style={{ width: "10px", maxWidth: "10px" }}>&nbsp;</TableCell>
                                <TableCell sx={{ color: "white", backgroundColor: "gray", paddingLeft: "5em", width: "70%" }}>Position</TableCell>
                                <TableCell sx={{ color: "white", backgroundColor: "gray", width: "3em", maxWidth: "3em" }}>Quantity</TableCell>
                                <TableCell sx={{ color: "white", backgroundColor: "gray", textAlign: "right", width: "3em", maxWidth: "3em" }}>Cost</TableCell>
                                <TableCell sx={{ color: "white", backgroundColor: "gray", textAlign: "right", width: "5em", maxWidth: "5em" }}>&nbsp;&nbsp;&nbsp;&nbsp;Price</TableCell>
                                <TableCell sx={{ color: "white", backgroundColor: "gray", textAlign: "right", paddingRight: "5em" }}>Profit/Loss</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {condors.map(condor => <PortfolioTableRow key={condor.description} condor={condor}></PortfolioTableRow>)}
                            <TableRow style={{ outline: "groove" }}>
                                <TableCell style={{ width: "10px", minWidth: "10px" }}>&nbsp;</TableCell>
                                <TableCell>Total P/L</TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell></TableCell>
                                <TableCell style={{ textAlign: "right", paddingRight: "5em", color: total >= 0 ? "black" : "red" }}>{formatDollarAmount(total)}</TableCell>
                            </TableRow>
                        </TableBody>
                    </Table>
                </TableContainer>
                : <div style={{marginTop:"3vh", color:"gray"}}>No portfolio condors</div> }
            </div>
        </div>
    );
}