import { createSlice } from '@reduxjs/toolkit'
import { subscribeTopic } from '../../services/StompClient';

const noTimeUntilTrading = {tradingStarted: false, tradingEnded: false, hours: '?', minutes: '?', seconds: '?'};

const tradingStatusSlice = createSlice({
    name: "tradingStatus",
    initialState: {
        tradingDay: false,
        alreadyTraded: null,
        bought: false,
        fillPrice: 0.0,
        tradingHalt: null,
        tradingEnabled: false,
        invalidCondor: false,
        timeUntilTrading: noTimeUntilTrading,
        condorBid: 0.0
    },
    reducers: {
        onTradingStateReceived: (state, action) => {
            state.tradingDay = action.payload.tradingDay;
            state.alreadyTraded = action.payload.alreadyTraded;
            state.unableToTrade = action.payload.unableToTrade;
            state.unableToTradeReason = action.payload.unableToTradeReason;
            state.bought = action.payload.bought;
            state.fillPrice = action.payload.boughtPrice;
            state.invalidCondor = action.payload.invalidCondor;
            state.expirationNotTradingDay = action.payload.expirationNotTradingDay;
            state.liveTrading = action.payload.liveTrading;
            const halt = action.payload.tradingHalt;
            if( halt != null && halt.start != null && halt.end != null ) {
                state.tradingHalt = halt;
            } else {
                state.tradingHalt = null;
            }
            state.tradingHaltOverridden = action.payload.tradingHaltOverridden;
            state.tradingEnabled = action.payload.tradingEnabled;
          },
          onTradingTimeReceived: (state, action) => {
            state.timeUntilTrading = action.payload;
          },
          onCondorBidReceived: (state, action) => {
            state.condorBid = action.payload;
          },
          onDisconnected: (state) => {
            state.tradingDay = null;
            state.alreadyTraded = null;
            state.bought = null;
            state.fillPrice = null;
            state.invalidCondor = null;
            state.tradingHalt = null;
            state.timeUntilTrading = null;
            state.condorBid = null;
          }
    }
});

export const subscribeTradingState = () => {
    return async (dispatch) => {
        subscribeTopic("/topic/trading.state", (tradingState) => {
            dispatch(onTradingStateReceived(tradingState));
        });
    }
}

export const subscribeTradingTime = () => {
    return async (dispatch) => {
        subscribeTopic("/topic/trading.time", (timeUntilTrading) => {
            dispatch(onTradingTimeReceived(timeUntilTrading));
        });
    }
}

export const subscribeCondorBid = () => {
    return async (dispatch) => {
        subscribeTopic("/topic/trading/condor.bid", (condorBid) => {
            dispatch(onCondorBidReceived(condorBid));
        });
    }
}

export const onConnectedChanged = (connected) => {
    return async (dispatch) => {
        if( !connected ) {
            dispatch(onDisconnected());
        }
    }
}

export const { onTradingStateReceived, onTradingTimeReceived, onCondorBidReceived, onDisconnected } = tradingStatusSlice.actions

export default tradingStatusSlice.reducer
